<template>
  <div id="User">
    <article>
      <h2></h2>
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="first">
          <el-form
            :model="userForm"
            :rules="rules"
            ref="userForm"
            label-width="100px"
          >
            <el-form-item label="">
              {{ userInfo.user }}
            </el-form-item>
            <el-form-item label="">
              <el-input type="password" v-model="userForm.oldPass"></el-input>
            </el-form-item>
            <el-form-item label="" prop="user">
              <el-input v-model="userForm.user"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="userNameUpdate"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-tab-pane>


        <el-tab-pane label="" name="second">
          <el-form
            :model="passForm"
            :rules="rules"
            ref="passForm"
            label-width="100px"
          >
            <el-form-item label="">
              <el-input type="password" v-model="passForm.oldPass"></el-input>
            </el-form-item>
            <el-form-item label="" prop="pass">
              <el-input type="password" v-model="passForm.pass"></el-input>
            </el-form-item>
            <el-form-item label="" prop="pass2">
              <el-input type="password" v-model="passForm.pass2"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="passUpdate"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>


        <el-tab-pane label="" name="third">
          <el-alert
            title=" + "
            type="warning">
          </el-alert>
          <el-upload
            class="avatar-uploader"
            :action="baseURL+'/user/avatar'"
            :show-file-list="false"
            :with-credentials="true"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
    </article>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "User",
  data() {
    return {
      activeName: "first",
      userForm: {
        user: "",
        pass:"",
        oldPass:""
      },
      passForm: {
        oldPass: "",
        pass: "",
        pass2: ""
      },
      rules: {
        //
        user: [
          {
            validator(rule, value, callback) {
              let reg = /^[^\s]{2,8}$/;

              if (reg.test(value)) {
                callback();
              } else {
                callback(new Error("2-8"));
              }
            },
            trigger: "blur",
          },
        ],

        pass: [
          {
            validator(rule, value, callback) {
              let reg = /^[\w,.?;<>/|\\:'"!@#$%^&*()+-]{6,16}$/;
              if (reg.test(value)) {
                callback();
              } else {
                callback(new Error("6-16"));
              }
            },
            trigger: "blur",
          },
        ],
        pass2: [
          {
            validator: (rule, value, callback) => {
              if (!value) {
                callback(new Error(""));
              } else if (value !== this.passForm.pass) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      },
      imageUrl: "",
    };
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
    ...mapMutations(["updateUserName",'updateUserInfo']),
    //
    userNameUpdate() {
      this.$refs.userForm.validate(async (v) => {
        console.log("")
        if (v) {
          let { data } = await this.$axios({
            method: "post",
            url: "/user/name",
            data: {
              user: this.userForm.user,
              oldPass: this.userForm.oldPass,
            },
          });

          if (data.code) {
            return this.$message.error(data.msg);
          }

          this.$message.success("");
          this.userForm.oldPass = "";
          this.updateUserName(this.userForm.user);
          this.userForm.user = "";
          this.userForm.pass = "";
        } else {
          return false;
        }
      });
    },


    //
    passUpdate() {
      this.$refs.passForm.validate(async v => {
        if (v) {
          console.log("")
          let {data} = await this.$axios({
            method: "POST",
            url: "/user/pass",
            data: {
              oldPass: this.passForm.oldPass,
              pass: this.passForm.pass
            }
          })
          console.log(data)
          if (data.code) {
            return this.$message.error(data.msg)
          }

          //vuex
          this.$message.success(data.msg)
          this.updateUserInfo({})
          this.$router.replace("/")

        } else {
          return false
        }
      })
    },



    //
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);

      if (res.code){
        return this.$message.error(res.msg)
      }

      //
      this.$message.success("")
      //vuex
      this.updateUserInfo(res.data)
    },


    //  
    beforeAvatarUpload(file) {
      let fileType = file.type
      const isLt2M = file.size / 1024 / 1024 < 2;
      const isJPG = /^image\/(jpeg|png)$/.test(fileType)

      if (!isJPG) {
        this.$message.error(' JPG,PNG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      console.log("")
      return isJPG && isLt2M;
    }




  },


  //user
  created() {
    if (!this.userInfo.user) {
      this.$router.replace("/")
    }
  }


};
</script>

<style lang="less" scoped>
#User {
  flex: 1;

  article {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;

    h2 {
      margin-bottom: 15px;
      font-size: 20px;
      letter-spacing: 3px;
      line-height: 24px;
      border-left: 5px solid #73b899;
      text-indent: 10px;
    }
  }


  /deep/ .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-top: 10px;
  }
  /deep/ .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  /deep/ .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  /deep/ .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>














